<template>
  <div id="app">
    <!-- Navbar Starts -->
    <navbar @search="search"></navbar>
    <!-- Navbar Ends -->

    <div class="main-section container my-5">
      <div class="row">
        <div class="col-8">
          <!-- Inventory Starts -->
          <router-view></router-view>
          <!-- <inventory @newItemAdded="addCartItem" :items="items"></inventory> -->
          <!-- Inventory Ends -->
        </div>
        <div class="col">
          <!-- Cart Starts -->
          <cart @itemRemoved="removeCartItem" :items="cart"></cart>
          <!-- Cart Ends -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Navbar from "./components/Navbar.vue";
// import Inventory from "./components/views/Inventory.vue";
import Cart from "./components/Cart.vue";
import data from "./data.js";

export default {
  components: {
    Navbar,
    // Inventory,
    Cart,
  },
  data() {
    return {
      items: [],
      cart: [],
    };
  },
  mounted() {
    this.items = data;
  },
  methods: {
    addCartItem(item) {
      this.cart.push(item);
    },
    removeCartItem(index) {
      this.cart.splice(index, 1);
    },
    search(keyword) {
      this.items = data.filter((item) => {
        return item.title.toLowerCase().indexOf(keyword.toLowerCase()) !== -1;
      });
    },
  },
};
</script>

<style>
</style>



