import Test from '../components/views/Test'
import Inventory from "../components/views/Inventory";
import ProductDetails from '../components/views/ProductDetails'

export const routes = [
    {
        path: '',
        component: Inventory
    },
    {
        path: '/product/:id',
        component: ProductDetails
    },
    {
        path: '/test/:id',
        component: Test
    }
]


