<template>
  <div v-if="!loading" class="row row-cols-1 row-cols-md-3 g-4">
    <div v-for="(item, index) in items" :key="index" class="col">
      <div class="card">
        <img :src="item.photo" class="card-img-top" :alt="item.title" />
        <div class="card-body">
          <router-link tag="h5" :to="{path: '/product/' + item.id}">
            <h5 class="card-title">{{ item.title }}</h5>
          </router-link>
          <p class="card-text">{{ item.price }}</p>
          <button @click="addToCart(item)" class="btn btn-primary">
            + Cart
          </button>
        </div>
      </div>
    </div>
  </div>
  <h1 v-else>Loading...</h1>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      loading: true
      // items: [],
    };
  },
  // props: ["items"],
  computed: {
    items() {
      return this.$store.getters.getInventory
    }
  },
  mounted() {
    this.fetchInventory();
  },
  methods: {
    addToCart(item) {
      // this.$emit("newItemAdded", item);
      // this.$store.commit('addToCart', item)
      this.$store.dispatch('addToCart', item)
    },
    fetchInventory() {
      var self = this;
      axios.get("http://localhost:3000/items").then((response) => {
        // self.items = resopnse.data;
        self.$store.commit('setInventory', response.data)
        self.loading = false;
      });
    },
  },
};
</script>

<style>
</style>


