<template>
  <nav class="navbar navbar-expand-lg navbar-light bg-light">
    <!-- <router-link :to="{path: '/'}">Hello</router-link>
        <router-link :to="{path: '/test/1'}">Test 1</router-link>
        <router-link :to="{path: '/test/2'}">Test 2</router-link>
        <router-link :to="{path: '/test/3'}">Test 3</router-link> -->
    <div class="container">
      <button
        class="navbar-toggler"
        type="button"
        data-bs-toggle="collapse"
        data-bs-target="#navbarTogglerDemo03"
        aria-controls="navbarTogglerDemo03"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon"></span>
      </button>
      <a class="navbar-brand" href="#">
        <router-link tag="h3" :to="{path: '/'}">First Vue Shop</router-link>
      </a>
      <div class="collapse navbar-collapse" id="navbarTogglerDemo03">
        <ul class="navbar-nav me-auto mb-2 mb-lg-0">
          <li class="nav-item">
            <a class="nav-link" href="#">Link</a>
          </li>
          <li class="nav-item">
            <a
              class="nav-link disabled"
              href="#"
              tabindex="-1"
              aria-disabled="true"
              >Disabled</a
            >
          </li>
        </ul>
        <form @submit.prevent="search" class="d-flex">
          <input v-model="keyword"
            class="form-control me-2"
            type="search"
            placeholder="Search"
            aria-label="Search"
          />
          <button class="btn btn-outline-success" type="submit">Search</button>
        </form>
      </div>
    </div>
  </nav>
</template>

<script>
import axios from 'axios'

export default {
  data() {
    return {
      keyword: ''
    }
  },
  methods: {
    search() {
      var self = this
      // this.$emit('search', this.keyword)
      axios.get('http://localhost:3000/search/' + this.keyword).then(response => {
        self.$store.commit('setInventory', response.data)
      })
    }
  }
}
</script>

<style>
</style>


