<template>
  <div class="card" style="width: 18rem">
    <div class="card-header fw-bold">Cart Items</div>
    <ul class="list-group list-group-flush">
      <li
        v-for="(item, index) in items"
        :key="index"
        class="list-group-item d-flex justify-content-between align-items-start"
      >
        <div class="ms-2 me-auto">
          <div>{{ item.title }}</div>
        </div>
        <span>${{ item.price }}</span>
        <span
          ><button
            @click="removeItem(index)"
            class="btn btn-danger btn-sm ms-2"
          >
            X
          </button></span
        >
      </li>
    </ul>
    <div class="card-footer d-flex justify-content-between align-items-start">
      <div class="ms-2 me-auto">
        <div class="fw-bold">Total</div>
      </div>
      <span>${{ totalPrice }}</span>
    </div>
    <button v-if="items.length > 0" @click="checkout" class="btn btn-success">
      Checkout
    </button>
  </div>
</template>

<script>
export default {
  // props: ["items"],
  computed: {
    items() {
      return this.$store.getters.getCart;
    },
    totalPrice() {
      var total = 0;
      this.items.forEach((item) => {
        total += parseFloat(item.price);
      });
      return total.toFixed(2);
    },
  },
  methods: {
    removeItem(index) {
      // this.$emit('itemRemoved', index)
      this.$store.commit("removeItem", index);
    },
    checkout() {
      if (confirm("Want to checkout?")) {
        this.$store.commit("clearCart");
      }
    },
  },
};
</script>

<style>
</style>


